import React, {Component} from 'react';
import PropTypes from 'prop-types';
import Chart from 'react-apexcharts'
const d3 = require('d3')
/**
 * ExampleComponent is an example component.
 * It takes a property, `label`, and
 * displays it.
 * It renders an input with the property `value`
 * which is editable by the user.
 */
export default class DashApexcharts extends Component {
  constructor(props) {
    super(props);
    const {id, xaxis, setProps, series, label, type, curve, options} = this.props
    options.dataLabels.formatter = function(value, { seriesIndex, dataPointIndex, w }) {
//    console.log(seriesIndex, dataPointIndex, options.series, options.series[seriesIndex], options.series[seriesIndex].format)
//    console.log(d3.format(options.series[seriesIndex].format,value))
    var returnstring = value
    try {returnstring = d3.format(options.series[seriesIndex].format)(value);}
    catch(err) {returnstring = value;}
    if(options.series[seriesIndex].format=='blank'){returnstring = ' ';}
    return returnstring}
  }

  render() {
    return (
    console.log(this.props),
      <Chart options={this.props.options} series={this.props.options.series} type={this.props.options.chart.type} width={this.props.options.chart.width} height={this.props.options.chart.height} />
    )
  }
}

DashApexcharts.defaultProps = {};

DashApexcharts.propTypes = {
    /**
     * The ID used to identify this component in Dash callbacks.
     */
    id: PropTypes.string,

    /**
     * A label that will be printed when this component is rendered.
     */
    label: PropTypes.string,

    /**
     * The ID used to identify this component in Dash callbacks.
     */
    type: PropTypes.string,

    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func,

    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    xaxis: PropTypes.array,

    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    series: PropTypes.array,
    curve: PropTypes.string,
    options: PropTypes.object,
};
